import {APIGatewayProxyEvent, APIGatewayProxyResult} from "aws-lambda";
import CryptoUtils from "./util/crypto-utils";
import {UserRepository} from "./repository/user-repository";
import { v4 as uuidv4 } from 'uuid';

class RegistrationHandler {
    private readonly SALT_LENGTH = 10;
    private repository = new UserRepository();

    public async handle(event: APIGatewayProxyEvent): Promise<APIGatewayProxyResult> {
        const userRegistration: UserRegistration = JSON.parse(event.body);
        const generatedSalt = CryptoUtils.generateSalt(this.SALT_LENGTH);

        const result = await this.repository.save({
            id: uuidv4(),
            ...userRegistration,
            salt: generatedSalt,
            hash: CryptoUtils.hash(userRegistration.password, generatedSalt)
        });

        return {
            statusCode: 201,
            body: JSON.stringify(result)
        }
    }
}

export interface UserRegistration {
    email: string,
    password: string
}

export const handler = new RegistrationHandler().handle;